// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "vendor";
@import "settings";
@import "ui";


// подключения компонентов страницы
@import "./components/header";

.tabs {
  @include tabs;
}


.main {
  flex: 1 1 auto;
  // .main_section

  &_section {
    padding: 25px 0;
  }

  // .main_wrapper
  &_wrapper {
    position: relative;
    z-index: 1;
    background-color: rgba(var(--color-background));
  }
}

.heroes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 100px;
  min-height: 100vh;
  color: rgba(var(--color-foreground));

  // .heroes_body
  &_body {
  }

  // .heroes_bg

  &_bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
  }

  // .heroes_content

  &_content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  // .heroes_text

  &_text {
    font-size: 16px;
    h1,h2,h3,h4 {
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
    p {
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }

  // .heroes_button

  &_button {
  }
}

.in-marvel {

  // .in-marvel_body

  &_body {
  }

  // .in-marvel_cells

  &_cells {
    @media (min-width: $md2 + px) {
      &:nth-child(2n){
        flex-direction: row-reverse;
      }
  }
  @media (max-width: $md2 + px) {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  } 
  }
}

.cells {
  display: flex;
  align-items: center;
  // .cells_item
  @media (max-width: $md2 + px) {
    flex-direction: column;
  }
  &_item {
    padding: 10px 40px 10px 40px;
    @media (max-width: $md2 + px) {
      padding: 0;
    }
  }

  // .cells_item--img

  &_item--img {
    width: 100%;
    flex: 0 0 50%;
    padding: 0 0 50% 0;
    img {
      border-radius: 6px;
    }
    @media (max-width: $md2 + px) {
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  // .cells_item--slider
  
  &_item--slider {
    min-width: 0; 
    flex: 0 0 50%;
    padding: 0;
    img {
      border-radius: 6px;
    }
    @media (max-width: $md2 + px) {
      margin-bottom: 20px;
      width: 100%;
    }
  }
}

.slider {

  // .slider_wrap

  &_wrap {
  }

  // .slider_slide

  &_slide {

  }

  &_slide-img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .slider_pagination

  &_pagination {
    margin-top: 12px;
  }
  // .slider_pagination-block

  &_pagination-block{
    margin-top: 8px;
    padding-bottom: 80%;
    &:hover {
      opacity: 0.7;
    }
    @media (max-width: $md2 + px) {
      &:not(:last-child) {
        margin-right: 20px;
      }
    }

  }
    // .slider_pagination-img

    &_pagination-img {
    }
  
      // .slider_wrapper
  
      &_wrapper {
      @media (min-width: $md2 + px) {
        display: grid;
        gap: 15px;
        grid-template-columns: repeat(4, 1fr);
      }
      }
  
      // .slider_pagination-slide
  
      &_pagination-slide {
        pointer-events: all !important;
        cursor: pointer;
      }
  }



.faq {

  // .faq_body

  &_body {
  }

  // .faq_title
  
  &_title {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  // .faq_items

  &_items {
  }

  // .faq_item

  &_item {
    &:not(:first-child){
      margin-top: 15px;
     }
  }

  // .faq_item-title

  &_item-title {
    font-weight: bold;
  }

  // .faq_item-body

  &_item-body {
    p {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  // .faq_item-title

  &_item-title {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.form-message {
  display: flex;
  flex-direction: column;

  // .form-message_item

  &_item {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
    &--select {
      width: 248px;
      height: 45px;
      padding: 0 15px;
      cursor: pointer;
      background-color: transparent;
      color: #585858;
      border: 1px solid rgba($color: #585858, $alpha: 1.0);
      border-radius: 5px;
      &:hover {
        border-width: 2px;
      }
    }
  }
}

.spollers {

  // .spollers_item
  
  &_item {
    &:first-child {
      border-top: 1px solid rgba($color: #585858, $alpha: 0.5);
    }
    border-bottom: 1px solid rgba($color: #585858, $alpha: 0.5);
  }

  // .spollers_title

  &_title {
    font-weight: bold;
    padding: 20px 0;
  }

  // .spollers_content

  &_content {
    padding: 20px 0;
  }
}
