.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10px 0;
    z-index: 3;
    background-color: var(--gradient-background);
    // .header_line

    &_line {
        display: flex;
        flex-direction: row;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }

    // .header_logo

    &_logo {
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    // .header_nav

    &_nav {
        flex: 1 1 auto;
        &:not(:last-child) {
            margin-right: 15px;
        }
        @media (max-width: $md2 + px) {
            display: none;
        }
    }

    // .header_list

    &_list {
        display: flex;
        justify-content: center;
    }

    // .header_list-item

    &_list-item {
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    // .header_list-link

    &_list-link {
        color: rgba(var(--color-foreground));
    }

    // .header_contacts

    &_contacts {
        margin-left: auto;
        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    // .header_phone

    &_phone {

    }
}

.header-logo {
    display: block;
    width: 60px;
    img {
        display: block;
        max-width: 100%;
        height: auto;
        vertical-align: top;
    }
    @media (min-width: $md2 + px) {
        width: 100px;
    }

}

.header-phone {
    color: white;
    font-size: 16px;
    font-weight: 600;
    // .header-phone_icon
    &:hover {
        opacity: 0.9;
        .header-phone_icon {
            color: red;
        }
    }
    &_icon {
        margin-right: 10px;
    }
    @media (min-width: $md2 + px) {
        font-size: 20px;
    }
}
